import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../auth.guard';
import { CanDeactivateGuard } from '../can-deactivate.guard';
import { WowsComponent } from './wows.component';
import { WowsPermissionGuard } from './wows-permission.guard';

import { WowsDashboardComponent } from './dashboard/wows-dashboard.component';

const wowsRoutes: Routes = [
  { path: '', component: WowsComponent, children: [
    { path: 'dashboard', component: WowsDashboardComponent, canActivate: [WowsPermissionGuard] },
    { path: '', redirectTo: 'dashboard', pathMatch:"full" },
  ]},
];

@NgModule({
  imports: [
  RouterModule.forChild(wowsRoutes)
  ],
  exports: [
  RouterModule
  ],
  declarations: []
})

export class WowsRoutingModule { 
  
}
