import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { WowsPermissionService } from './wows-permission.service';

@Injectable({
  providedIn: 'root'
})

export class WowsPermissionGuard implements CanActivate {

    constructor(
        private router: Router,
        private wowsPermissionService: WowsPermissionService,
    ) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        var res:boolean = this.wowsPermissionService.passPermission(state.url);
        if(!res) {
            this.router.navigate(['/login'], { queryParams: { returnUrl: state.url }});
        }
        return res;
    }
}
