import { NgModule, CUSTOM_ELEMENTS_SCHEMA} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule }    from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { CommonModule }   from '@angular/common';
//import { MatFileUploadModule } from 'angular-material-fileupload';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatChipsModule } from '@angular/material/chips';
import { WowsRoutingModule } from './wows-routing.module';
import { MaterialModule } from '../material/material.module';
import { xFilterModule } from '../xfilter/xfilter.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { WowsComponent } from './wows.component';

import { AuthService } from '../auth.service';
import { AuthInterceptor } from '../auth.interceptor'

import { WowsDashboardComponent } from './dashboard/wows-dashboard.component';


import { Panel } from '../navigation/panel/panel';
import { PanelItem } from '../navigation/panel/panel';
import { PanelService } from '../navigation/panel/panel.service';
import { xFilterDialogComponent } from '../xfilter/xfilter.component';
import { xFilterDialogNumberComponent } from '../xfilter/xfilter.component';
import { xFilterDialogDateComponent } from '../xfilter/xfilter.component';
import { xFilterDialogTextComponent } from '../xfilter/xfilter.component';
import {WowsPermissionService } from './wows-permission.service';

import { ChartModule } from 'angular-highcharts';

@NgModule({
  declarations: [
  WowsComponent,
  WowsDashboardComponent,
  ],
  imports: [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
  HttpClientModule,
  WowsRoutingModule,
  MaterialModule,
  FlexLayoutModule,
  //MatFileUploadModule,
  ChartModule,
  MatGridListModule,
  xFilterModule,
  MatSlideToggleModule,
  MatChipsModule,
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
  ],
  bootstrap: [],
  entryComponents: [
  xFilterDialogComponent,
  xFilterDialogNumberComponent,
  xFilterDialogDateComponent,
  xFilterDialogTextComponent,
  ],
})

export class PeModule { 
  
  constructor (
    private panelService: PanelService,
    private wowsPermission: WowsPermissionService,
    private authService: AuthService,
    ) {

    this.authService.currentUser.subscribe(res => {
      this.panelService.messageSource.next(new Panel(
        "WOWS", 2, [
        new PanelItem("Dashboard", "wows/dashboard", "dashboard", this.wowsPermission.passPermission("wows/dashboard")),
        new PanelItem("Rig Barchart", "wows/rigchart", "bar_chart", this.wowsPermission.passPermission("wows/rigchart")),
        ]));
    });

  }
}
